.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.Row
{
  display: table;
  width: 100%; /*Optional*/
  height: 100px;
}
.Column
{
  display: table-cell;
  width: 20%;
  text-align: center;
}
.CenterColumn
{
  display: table-cell;
  vertical-align: top;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.logo{
  position: relative;
  width: 100%;
  height: 40px;
  color: black;
  font-family: 'DejaVu Sans', Arial, Helvetica, sans-serif;
  font-size: xx-large;
  background: darkseagreen;
}

.periodicTableContainer{
  text-align: center;
  padding-top: 5px;
  min-width: 700px;
  max-width: 700px;
  margin-left:auto;
  margin-right:auto;
}

.textContainer{
  font-family: 'DejaVu Sans', Arial, Helvetica, sans-serif;
  font-size: large;
  background: lightgrey;
  width: 100%;
  height: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 3px;
}




